@import "jquery.formstyler";
@import "jquery.formstyler.theme";
@import "functions";
@import "variables";
@import "layout/normalize";
@import "components";

/*Common*/

* {
	margin: 0;
	padding: 0;

	outline: 0;
	box-sizing: inherit;

	&:before,
	&:after {
		box-sizing: inherit; } }

html {
	-webkit-tap-highlight-color: transparent;
	font-family: sans-serif;
	font-size: 16px; }


body, html {
	box-sizing: border-box;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	min-width: 320px;
	overflow-x: hidden; }

img {
	max-width: 100%;
	height: auto; }

body {
	-ms-overflow-style: scrollbar;
	font-size: 1rem;
	font-weight: normal;
	position: relative;
	width: 100%;
	height: 100%; }


button {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	border: 0;
	background-color: transparent;
	cursor: pointer; }

textarea {
	resize: none; }
input {
	&:-webkit-autofill {
		-webkit-box-shadow: 0 0 0 30px #ecedef inset;
		&:hover, &:focus, &:active {
			-webkit-box-shadow: 0 0 0 30px #ecedef inset; } } }

a, a:active, a:focus,
button, input, button:hover, button:active, button:focus,
input:focus, input:active, textarea {
	text-decoration: none;
	outline: none; }
a:visited {
	color: inherit; }

/* header */
.header {
	padding: 14px 0 16px;
	box-shadow: 0 3px 6px rgba(48, 99, 149, 0.16);
	&-abs {
		color: #fff;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		width: 100%;
		background-color: transparent;
		box-shadow: unset;
		border: 1px solid rgba(255,255,255,.35);
		.main-menu {
			ul {
				a {
					color: #fff;
					&:after {
						display: block;
						content: attr(data-title);
						font-weight: 700;
						height: 1px;
						color: transparent;
						overflow: hidden;
						visibility: hidden;
						margin-bottom: -1px; } }
				li {
					&.current-menu-item, &:hover {
						&:after {
							background-image: url(../images/triangle-white.svg); } } } } }
		.contacts {
			&__tel {
				&:before {
					background-image: url(../images/ico_phone-white.svg); } }
			&__mail {
				&:before {
					background-image: url(../images/ico_mail-white.svg); } } } }
	&-wrap {
		display: flex;
		justify-content: space-between; }
	.contacts {
		margin-top: 3px;
		margin-right: 6px;
		a {
			&:not(:last-of-type) {
				margin-bottom: 11px; } } }
	&-nav {
		display: flex;
		align-items: center; }
	.logo {
		margin-right: 15px; } }
.logo {
	max-width: 180px; }
.main-menu {
	ul {
		display: flex;
		list-style: none;
		flex-wrap: wrap;
		justify-content: center;
		li {
			&:not(:last-of-type) {
				margin-right: get-vw(47px); }
			&.current-menu-item, &:hover {
				position: relative;
				a {
					font-weight: 500; } } }
		a {
			display: inline-block;
			color: $color-txt;
			font-size: toRem(16px);
			font-weight: 300;
			line-height: 30px; } } }
