body {
	font-family: "Roboto", sans-serif;
	color: $color-txt; }
h1 {
	font-size: toRem(55px);
	line-height: toEm(55px, 69px);
	font-weight: 700;
	margin-top: 0; }


h2 {
	font-size: toRem(32px);
	line-height: 1.2em;
	font-weight: 700; }

h3 {
	font-size: toRem(24px);
	font-weight: 500; }

h4 {
	font-size: toRem(20px);
	font-weight: 500; }

h5 {
	font-size: toRem(18px);
	font-weight: 500; }

h6 {
	font-size: toRem(16px);
	font-weight: 500; }

p {
	font-size: 1rem;
	line-height: toEm(16px, 26px);
	font-weight: 400; }

a {
	color: inherit; }
small {
	font-size: 10px; }
.clearfix::after {
  content: "";
  display: table;
  clear: both; }


.container {
	max-width: 1170px;
	padding: 0 15px;
	margin: 0 auto; }

/*components*/

.common-wrap {
	display: flex;
	flex-wrap: wrap; }

/*buttons*/
.btns-wrap {
	@extend %flex-wrap;
	justify-content: center;
	.btn {
		margin: 0 10px 15px; } }

.btn {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 187px;
	height: 39px;
	border-radius: 32px;
	color: #fff;
	font-size: 14px;
	font-weight: 500;
	line-height: toEm(14px, 30px);
	text-transform: uppercase;
	text-align: center;
	&-common {
		background-image: linear-gradient(68deg, #00aeef 0%, #36c5c0 100%);
		&:hover {
			background-image: none;
			background-color: #00aeef; } }
	&-base-gradient {
		background-image: linear-gradient(-56deg, #226ec4 0%, #9438c1 100%);
		&:hover {
			background-image: none;
			background-color: #226ec4; } }
	&_set, &_print {
		position: relative;
		&:before {
			content: '';
			background-position: center center;
			background-repeat: no-repeat;
			background-size: auto;
			margin-right: 8px;
			display: flex;
			align-items: center;
			width: 17px;
			height: 18px; } }
	&_set {
		&:before {
			background-image:  url(../images/ico_settings.svg); } }
	&_print {
		&:before {
			background-image:  url(../images/ico_print.svg); } }
	&:visited {
		color: #fff; } }


.wp-pagenavi {
	list-style: none;
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	align-items: flex-end;
	justify-content: center;
	margin: 0 auto;
	a {
		font-size: toRem(18px);
		color: #84aed8;
		font-weight: 500;
		min-width: 43px;
		height: 43px;
		border-radius: 2px;
		border: 1px solid transparent;
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0 10px;
		&:hover {
			border-color: #edc116;
			color: #edc116; } }
	.current {
		font-weight: 500;
		border-radius: 2px;
		min-width: 43px;
		height: 43px;
		display: flex;
		justify-content: center;
		align-items: center;
		color: #fff;
		border: 1px solid #fff; }
	.prev, .next {
		border-radius: 2px;
		border: 1px solid #fff;
		color: #fff;
		width: 134px;
		height: 43px;
		padding: 0 10px;
		&:hover {
			color: #edc116;
			border-color: #edc116; } } }

.pagination-wrap {
	margin: 0 auto; }
.pagination-blue {
	.wp-pagenavi {
		.current {
			color: #84aed8;
			border: 1px solid #84aed8; }
		.prev, .next {
			color: #84aed8;
			border: 1px solid #84aed8;
			&:hover {
				color: #edc116;
				border-color: #edc116; } } } }

.tabs__caption {
	list-style: none;
	li {
		cursor: pointer; } }
.tabs__content {
	display: none;

	&.active {
		display: block; } }

.input {
	&-group {
		display: flex;
		flex-wrap: wrap; }
	&-field {
		margin: 0 15px 15px;
		.btn {
			height: 42px; }
		label {
			display: block;
			font-weight: 500;
			margin-bottom: 10px; }
		input, textarea {
			width: 100%;
			height: 42px;
			border-radius: 21px;
			background-color: #fff;
			padding: 0 22px;
			border: none;
			outline: 0;
			&:focus {
				border: 1px solid $color-base; } }
		textarea {
			height: 137px;
			padding: 22px; } } }






/* Hide HTML5 Up and Down arrows. */
input[type="number"]::-webkit-outer-spin-button, input[type="number"]::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0; }
input[type="number"] {
	-moz-appearance: textfield; }

.map {
	&-wrap {
		iframe {
			width: 100%;
			height: 100%; } } }

.base-gradient {
	background-image: linear-gradient(-70deg, #13599f 0%, #9438c1 100%); }
/* breadcrumbs */
.breadcrumbs {
	padding: 28px 0 20px;
	border-bottom: 1px solid $color-light-grey;
	ul {
		list-style: none;
		display: flex;
		flex-wrap: wrap;
		li {
			font-size: toRem(18px);
			color: $color-base;
			line-height: toEm(18px, 30px);
			font-weight: 500;
			&:not(:last-of-type) {
				margin-right: 20px;
				&:after {
					content: '>';
					color: $color-txt;
					margin-left: 25px; } }

			a {
				font-weight: 400;
				color: $color-txt; } } } }


.common-section {
	padding: 30px 0; }

.slider {
	position: relative;
	.slider-btn {
		z-index: 10;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		width: 41px;
		height: 45px;
		background-color: transparent;
		@extend %bg-auto;
		&-prev {
			left: 0;
			//background-image: url()
			&:hover {
				//background-image: url()
				@media screen and (max-width: 1024px) {
 } } }					//background-image: url()
		&-next {
			right: 0;
			//background-image: url()
			&:hover {
				//background-image: url()
				@media screen and (max-width: 1024px) {
