			&.slider-btn-next {
				//background-image: url()
				&:hover {
					//background-image: url()
					@media screen and (max-width: 1024px) {
 } } } } } }						//background-image: url()

.sidebar {
	max-width: 263px;
	margin-right: 30px;
	&-item {
		margin-bottom: 36px;
		h5 {
			text-transform: uppercase;
			color: $color-base;
			border-bottom: 1px solid $color-base;
			padding-bottom: 8px;
			margin-bottom: 15px; }

		ul {
			list-style: none; }
		li {
			margin-bottom: 15px;
			font-weight: 300; }
		a {
			&:hover {
				text-decoration: underline; } } }
	.btn-close {
		position: absolute;
		top: 5px;
		right: 5px; } }

.plus-minus-group {
	background-color: $color-mid-grey;
	width: 88px;
	height: 29px;
	border-radius: 20px;
	input {
		background-color: transparent;
		width: 48px;
		border: 0;
		outline: 0;
		text-align: center;
		font-weight: 500;
		color: #333; }
	button {
		width: 20px;
		min-width: 20px;
		border-radius: 20px;
		&.disable {
			opacity: 0.3;
			cursor: not-allowed; } } }



.contacts {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	a {
		position: relative;

		&:before {
			content: '';
			position: absolute;
			top: 6px;
			left: 0;
			z-index: 5;
			@extend %bg-contain; } }
	&__tel {
		padding-left: 37px;
		font-size: toRem(22px);
		font-weight: 500;
		line-height: 30px;
		white-space: nowrap;
		&:hover {
			text-decoration: underline; }
		&:before {
			width: 17px;
			height: 17px;
			background-image: url(../images/ico_phone.svg); } }
	.contacts__mail {
		white-space: nowrap;
		padding-left: 26px;
		font-size: toRem(18px);
		font-weight: 400;
		&:hover {
			text-decoration: underline; }
		&:before {
			width: 14px;
			top: 50%;
			transform: translateY(-50%);
			height: 10px;
			background-image: url(../images/ico_mail.svg); } }
	.contacts__map {
		padding-left: 26px;
		font-size: toRem(18px);
		font-weight: 400;
		&:before {
			top: 3px;
			width: 10px;
			height: 13px;
			background-image: url(../images/ico_map.svg); } } }

.section-head {
	small {
		color: $color-base;
		font-weight: 700;
		display: block;
		margin-bottom: 8px;
		letter-spacing: 0.55em;
		text-transform: uppercase; }
	h2 {
		display: inline-block;
		font-size: toRem(30px);
		color: $color-txt;
		font-weight: 700;
		position: relative;
		letter-spacing: 0.015em;
		text-transform: uppercase;
		&:after {
			content: '';
			position: absolute;
			top: 15px;
			left: calc(100% + 15px);
			width: 149px;
			height: 1px;
			background-color: $color-base; } } }


.flex-wrap {
	display: flex;
	flex-wrap: wrap; }

.soc-links {
	a {
		display: inline-block;
		&:not(:last-of-type) {
			margin-right: 10px; }
		svg {
			fill: #6684ab; }
		&:hover {
			svg {
				fill: #fff; } } } }




.contact-info-list {
	list-style: none;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin: 60px -40px 0;
	li {
		text-align: center;
		margin: 0 40px 15px;
		max-width: 250px;
		div {
			margin-bottom: 10px; }
		img {
			display: inline-block;
			margin-right: 15px; }
		span {
			color: $color-base;
			font-size: toRem(24px);
			font-weight: 500;
			line-height: 1.5; }
		p,a {
			font-size: toRem(20px);
			font-weight: 500; }
		a {
			&:hover {
				text-decoration: underline; } } } }

.footnote {
	font-size: 12px;
	line-height: 18px;
	color: #8d9aab;
	a {
		color: $color-base; } }
.popup {
	padding: 0;
	border-radius: 5px;
	&__top {
		padding: 90px 15px 25px;
		position: relative;
		color: #fff;
		text-align: center;
		background-image: linear-gradient(-70deg, #13599f 0%, #853aac 100%);
		&:before {
			content: '';
			position: absolute;
			top: 25px;
			left: 50%;
			transform: translateX(-50%);
			width: 60px;
			height: 60px;
			background-position: center center;
			background-repeat: no-repeat;
			background-size: contain; }
		h1,h2,h3,h4,h5 {
			font-weight: 500; } }
	&_order {
		.popup__top {
			&:before {
				background-image: url(../images/ico_order.svg); } }
		.btn {
			width: 377px; } }
	&_gratitude {
		width: 490px;
		.popup__top {
			padding-top: 116px;
			padding-bottom: 60px;
			h2 {
				font-size: toRem(36px); }
			&:before {
				background-image: url(../images/ico_checked.svg); } }
		.btn {
			width: 377px; } }
	.input {
		&-group {
			flex-direction: column;
			align-items: center;
			input {
				background-color: #e7eefc;
				&::placeholder {
					color: rgba(0,0,0,.7); } } }
		&-field {
			width: 377px;
			margin-bottom: 22px; } }
	&__bottom {
		padding: 35px 42px 25px; }
	.footnote {
		text-align: center;
		margin-top: 25px; }
	.fancybox-button {
		svg {
			path {
				fill: #fff; } } }
	&_set-props {
		.set-custom-props {
			margin-top: 0;
			margin-bottom: 0;
			&__title {
				span {
					svg {
						width: 30px !important;
						height: 30px !important; } } }
			.input-field {
				width: auto;
				input {
					background-color: #fff; }
				.btn {
					width: 183px;
					height: 35px; } }
			select {
				width: 100%; } }
		.jq-selectbox {
			width: 467px;
			span {
				display: inline-block;
				width: 20px;
				height: 20px;
				background-color: red; } } } }

.jq-selectbox {
	width: 100%;
	&.opened {
		.jq-selectbox__select {
			border-radius: 10px 10px 0 0; } }
	&__select {
		border-radius: 10px; }
	&__trigger {
		border-left: transparent; }
	&__dropdown {
		position: static;
		margin: 0;
		border-radius: 0 0 10px 10px; } }


.custom-select {
	position: relative;
	background-color: #ffffff;
	padding: 10px 20px;
	border-radius: 10px;
	.btn-drop {
		position: absolute;
		top: 3px;
		right: 5px; }
	ul {
		list-style: none;
		width: 467px;
		&.active {
			li {
				display: flex; } } }
	li {
		display: flex;
		align-items: center;
		justify-content: space-between;
		cursor: pointer;
		padding: 0 10px;
		&:first-of-type {
			padding-right: 32px; }
		&:not(:first-of-type) {
			margin-top: 10px;
			display: none; }
		&:hover {
			background-color: $color-light-grey;
			@media screen and (max-width: 1024px) {
				background-color: transparent; } }
		p {
			text-align: left;
			flex: 1; }

		.custom-select__button {
			display: inline-block;
			width: 10px;
			height: 10px;
			border: 1px solid $color-base;
			background-color: #ffffff;
			margin-right: 10px;
			border-radius: 50%;
			position: relative; }
		&.selected {
			p, span {
				font-weight: 500; }
			.custom-select__button {
				&:after {
					content: '';
					top: 50%;
					left: 50%;
					position: absolute;
					width: 6px;
					height: 6px;
					border-radius: 50%;
					background-color: $color-base;
					transform: translate(-50%, -50%); } } }
		span {
			color: $color-base;
			display: inline-block;
			margin-left: 10px; } } }

.btn-drop {
	display: inline-block;
	width: 45px;
	height: 35px;
	z-index: 10;
	background-image: url(../images/ico_angle.svg);
	@extend %bg-auto;
	border-radius: 5px;
	&:hover {
		background-color: rgba(0,0,0,.1); } }
.btn-close {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 20px;
	height: 20px; }

.btn-sidebar {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 45px;
	height: 45px;
	background-image: url(../images/ico_dots.svg), linear-gradient(-56deg, #226ec4 0%, #9438c1 100%);
	@extend %bg-auto;
	background-color: $color-base;
	position: absolute;
	z-index: 30;
	top: 10px;
	left: 10px;
	border-radius: 8px;
	box-shadow: 5px 5px 15px rgba(0,0,0,0.3); }


.btn-mob-menu {
	position: fixed;
	top: 15px;
	right: 15px;
	z-index: 101;
	width: 40px;
	height: 40px;
	background-image: url(../images/ico_mob-menu.png);
	@extend %bg-contain;
	border-radius: 50%;
	background-color: #fff;
	box-shadow: 2px 2px 5px rgba(0,0,0,0.3);
	&.active {
		background-size: 35px 35px;
		background-image: url(../images/ico_cancel-circle.svg); } }


.btn-to-up {
	display: none;
	position: fixed;
	right: 50px;
	bottom: 50px;
	width: 40px;
	height: 40px;
	background-image: linear-gradient(-56deg, #226ec4 0%, #9438c1 100%);
	border-radius: 50%;
	box-shadow: 4px 4px 10px rgba(0,0,0,0.3);
	&:hover {
		background-image: none;
		background-color: #226ec4; }
	svg {
		width: 20px;
		height: 20px;
		fill: #fff; }
	@media screen and (max-width: 768px) {
		right: get-vw-x(50px, 768); } }



